:root {
  --color-primary: #2186e1;
  --color-outline: #dee2e6;
  --color-success: rgb(25, 135, 84);
  --color-danger: rgb(220, 53, 69);
  --color-warning: rgb(255, 193, 7);
}

.ac-back-button {
  border: 2px solid var(--color-primary);
  color: #000;
  text-align: center;
  background-color: transparent;
  border-radius: 15px;
  margin-top: 2rem;
  padding: 0.75rem 1.5rem;
  font-weight: 600;
  transition: all 0.3s;
  box-shadow: 0 3px 7px rgba(33, 134, 225, 0.36);
  line-height: inherit;
  cursor: pointer;
  border: 0;
  text-decoration: none;
  display: inline-block;
}

.select-selected {
  background-color: white;
  border-radius: 10px;
  border: 1.5px solid transparent;
  border-color: #dee2e6;
  min-height: 38px;
}

/* Style the arrow inside the select element: */
.select-selected:after {
  position: absolute;
  content: ' ';
  top: 14px;
  right: 10px;
  width: 0;
  border: 6px solid transparent;
  border-color: #fff transparent transparent transparent;
}

/* style the items (options), including the selected item: */
.select-items .select-option {
  padding: 8px 16px;
  cursor: pointer;
  color: var(--text);
}

.select-selected {
  padding: 8px 16px;
  cursor: pointer;
  transition: all 300ms ease;
}

.select-clear {
  border-radius: 8px;
  color: var(--color-danger);
  padding: 4px 16px;
  cursor: pointer;
  display: flex;
  gap: 1rem;
  margin: 0.25rem;
  border: 2px solid var(--color-danger);
  transition: all 100ms ease-in-out;
  font-weight: 600;
}

.select-clear:hover {
  background-color: var(--color-danger);
  color: white;
}

/* Style items (options): */
.select-items {
  position: absolute;
  margin-top: -20px;
  border-radius: 10px;
  border: 1.5px solid transparent;
  border-color: #dee2e6;
  background-color: white;
  overflow: hidden;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
  opacity: 0;
  transition: all 0.3s cubic-bezier(0.12, 0.08, 0.58, 1.91);
  pointer-events: none;
  max-height: 300px;
  overflow: auto;
}
.ac-select-wrapper.active .select-items {
  opacity: 1;
  margin-top: 8px;
  pointer-events: auto;
}

.select-items .select-option:hover,
.same-as-selected {
  background-color: rgba(0, 0, 0, 0.1);
}

.ac-label {
  color: #5e5e5e;
  display: inline-block;
  top: 0%;
  bottom: 0%;
  left: 0%;
  right: auto;
}

.ac-label.floating-label {
  border-radius: 10px;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  font-weight: 600;
  display: flex;
}

.ac-field-wrapper.floating-label {
  margin-top: 1rem;
  margin-bottom: 1rem;
  display: block;
  position: relative;
}

.ac-label.floating-label.date {
  pointer-events: none;
  text-align: center;
  cursor: text;
  background-color: #fff;
  flex-direction: column;
  justify-content: center;
  margin: 6px;
  padding-left: 0;
  padding-right: 0;
  display: flex;
  position: absolute;
  top: 0;
  bottom: auto;
  left: 0;
  right: 0;
  transition: all 0.1s ease-in-out;
}

.ac-field-wrapper .ac-select-wrapper.active .ac-select-arrow,
.ac-datepicker-header-select.active .ac-datepicker-select-arrow {
  transform: rotate(0deg) !important;
}

.ac-field-wrapper .ac-select-wrapper.inactive:hover .ac-select-arrow,
.ac-field-wrapper .ac-select-wrapper.answered:hover .ac-select-arrow,
.ac-datepicker-header-select:not(.active):hover .ac-datepicker-select-arrow {
  transform: rotate(145deg);
}

.ac-field-wrapper .ac-select-wrapper.answered:not(.valid):not(.invalid) .ac-select-arrow,
.ac-field-wrapper .ac-select-wrapper.active:not(.valid):not(.invalid) .ac-select-arrow,
.ac-field-wrapper .ac-select-wrapper.inactive:not(.valid):not(.invalid):hover .ac-select-arrow,
.ac-date-input-wrapper.active .ac-label.floating-label.date,
.ac-date-input-wrapper .ac-date-input:not(:placeholder-shown) ~ .ac-label.floating-label.date {
  color: var(--color-primary);
}

.ac-field-wrapper.floating-label .ac-label {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  pointer-events: none;
  transition: all 0.1s ease-in-out;
}

.ac-field-wrapper.floating-label .ac-textfield {
  padding: 10px 20px;
  outline: none;
}

.ac-field-wrapper.floating-label.phone .ac-label {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  margin-left: 50px;
}

.ac-field-wrapper.floating-label.phone .ac-textfield {
  padding-left: 55px;
  outline: none;
}

.ac-field-wrapper.floating-label .ac-textfield:not(:placeholder-shown) ~ label,
.ac-field-wrapper.floating-label .ac-textfield:focus ~ label,
.ac-field-wrapper.floating-label .ac-select-wrapper.active ~ label,
.ac-field-wrapper.floating-label .ac-select-wrapper.answered ~ label {
  top: -8px;
  bottom: auto;
  left: 20px;
  background: white;
  padding: 0 10px;
  font-size: 14px;
  color: var(--color-primary);
  line-height: 1;
}

.ac-field-wrapper.floating-label .ac-textfield:hover:placeholder-shown:not(:focus) ~ label,
.ac-field-wrapper.floating-label .ac-select-wrapper.inactive:not(.answered):hover ~ label {
  top: -4px;
  left: 5px;
  font-size: 16px;
  color: var(--color-primary);
}

.ac-field-wrapper .ac-select-wrapper.inactive:hover .select-selected,
.ac-field-wrapper .ac-select-wrapper.answered .select-selected,
.ac-field-wrapper .ac-select-wrapper.active .select-selected,
.ac-field-wrapper .ac-textfield:hover {
  border-color: var(--color-primary);
}

.ac-field-wrapper .ac-textfield:not(:placeholder-shown),
.ac-field-wrapper .ac-select-wrapper.answered .select-selected,
.ac-field-wrapper .ac-select-wrapper.active .select-selected {
  border-color: var(--color-primary);
  padding-top: 12px;
}

.valid,
.valid .select-selected {
  border-color: var(--color-success) !important;
  color: var(--color-success) !important;
}

.invalid,
.invalid .select-selected {
  border-color: var(--color-danger) !important;
  color: var(--color-danger) !important;
}

.valid label,
.ac-select-wrapper.valid ~ label,
.valid .ac-feedback,
.ac-select-wrapper.valid ~ .ac-feedback {
  color: var(--color-success) !important;
}

.invalid label,
.ac-select-wrapper.invalid ~ label,
.invalid .ac-feedback,
.ac-select-wrapper.invalid ~ .ac-feedback {
  color: var(--color-danger) !important;
}

.valid .ac-feedback,
.invalid .ac-feedback,
.ac-select-wrapper.valid ~ .ac-feedback,
.ac-select-wrapper.invalid ~ .ac-feedback {
  display: block !important;
  opacity: 1 !important;
  transform: translateY(0) !important;
}

.iti__selected-flag {
  padding-right: 10px;
  padding-left: 10px;
  border-radius: 10px;
}

.iti__flag {
  margin-right: 3px;
  border-radius: 2px;
}

.iti__search-input {
  display: none;
}

.ac-datepicker-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 10;
  border: 1px solid #ccc;
  background-color: #fff;
  padding: 10px;
}

.ac-datepicker-container {
  z-index: 10;
  cursor: default;
  background-color: #fff;
  border-radius: 20px;
  margin-top: -40px;
  padding: 10px;
  position: absolute;
  top: auto;
  bottom: auto;
  left: 0%;
  box-shadow: 0 4px 10px rgba(5, 10, 41, 0.17);
  transition: all 0.3s cubic-bezier(0.24, 0.11, 0.47, 1.48);
  opacity: 0;
  height: 0px;
  overflow: hidden;
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
ac-datepicker-container.active .ac-datepicker-header-wrapper {
  justify-content: center;
  align-items: center;
  font-weight: 700;
  display: flex;
  position: relative;
}

.ac-datepicker-header-select {
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  color: var(--color-primary);
  cursor: pointer;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 5px;
  margin-right: 5px;
  padding: 5px 10px;
  display: flex;
  border-radius: 10px;
  border: 1.5px solid transparent;
  transition: all 0.4s cubic-bezier(0.24, 0.11, 0.47, 1.48);
}

.ac-datepicker-header-select:hover {
  border: 1.5px solid var(--color-primary);
}

.ac-datepicker-header-select.active {
  color: white;
  background-color: var(--color-primary);
  border: 1.5px solid var(--color-primary);
}

.ac-datepicker-select-arrow {
  width: 16px;
  pointer-events: none;
  flex-direction: row;
  align-items: center;
  transition: transform 0.3s cubic-bezier(0.24, 0.11, 0.47, 1.48);
  display: flex;
  transform: rotate(180deg);
}

.ac-datepicker-header-select-wrapper {
  z-index: 20;
  background-color: #fff;
  border-radius: 10px;
  margin-top: 0px;
  padding: 10px;
  display: block;
  opacity: 0;
  position: absolute;
  top: 0%;
  bottom: auto;
  left: auto;
  right: auto;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  pointer-events: none;
  transition: all 0.3s cubic-bezier(0.12, 0.08, 0.58, 1.91);
}

.ac-datepicker-header-select.active + .ac-datepicker-header-select-wrapper {
  opacity: 1;
  pointer-events: auto;
  margin-top: 50px;
}

.ac-datepicker-header-options-wrapper {
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  grid-template-rows: auto auto;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-auto-columns: 1fr;
  display: grid;
}

.ac-datepicker-header-options-wrapper.month {
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.ac-datepicker-header-select-option {
  width: 100%;
  height: 100%;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  font-weight: 400;
  transition: transform 0.1s;
  display: flex;
}

.ac-datepicker-header-select-option:hover {
  transform: scale(1.07);
}

.ac-datepicker-options-year-select {
  grid-column-gap: 5px;
  grid-row-gap: 5px;
  color: var(--color-primary);
  align-items: center;
  font-weight: 600;
  display: flex;
  flex-direction: row;
}

.ac-datepicker-dayofweek-wrapper {
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-auto-columns: 1fr;
  justify-content: space-around;
  align-items: center;
  justify-items: center;
  display: grid;
}

.ac-datepicker-dayofweek {
  width: 100%;
  height: 100%;
  min-height: 40px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5px 10px;
  font-weight: 600;
  display: flex;
}

.ac-datepicker-days-wrapper {
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-auto-columns: 1fr;
  align-content: center;
  justify-content: center;
  align-items: center;
  justify-items: center;
  display: grid;
}

.ac-datepicker-day {
  width: 100%;
  height: 100%;
  aspect-ratio: 1/1;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px 7px;
  transition: transform 0.1s;
  display: flex;
}

.ac-datepicker-day.empty {
  pointer-events: none;
  cursor: auto;
}

.ac-datepicker-day:hover {
  transform: scale(1.1);
}

.ac-datepicker-header-wrapper .ac-datepicker-options-year-header,
.ac-datepicker-header-wrapper .ac-datepicker-options-header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 5px 0;
}

.ac-datepicker-header-wrapper .ac-datepicker-options-header {
  justify-content: center;
}

#calendarHeader button {
  cursor: pointer;
}

.ac-text-button {
  color: var(--color-primary);
  background-color: rgba(56, 152, 236, 0);
  justify-content: center;
  align-items: center;
  padding: 4px 10px;
  display: flex;
  cursor: pointer;
}

.ac-text-button.disabled {
  opacity: 0.3;
  pointer-events: none;
  cursor: default;
}

.ac-date-input-wrapper.active,
.ac-date-input-wrapper.answered {
  border-color: var(--color-primary);
}

.ac-date-input-wrapper .ac-date-input:not(:placeholder-shown) ~ .ac-label.floating-label.date,
.ac-date-input-wrapper.active .ac-label.floating-label.date {
  transform: translateY(-20px);
  max-width: 60%;
  margin-left: auto;
  margin-right: auto;
}

.ac-date-input-wrapper.active + .ac-datepicker-container {
  margin-top: 10px;
  opacity: 1;
  pointer-events: auto;
  height: auto;
}

.ac-date-input-wrapper {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.ac-selection-field {
  grid-column-gap: 8px;
  grid-row-gap: 8px;
  cursor: pointer;
  display: flex;
  position: relative;
  color: black;
}

.ac-selection-field input {
  display: none;
}

.ac-selection-field .ac-selection-icons.ac-checked {
  display: none;
}

.ac-selection-bg {
  width: 100%;
  height: 100%;
  position: absolute;
  background-image: linear-gradient(158deg, #3f98e9, var(--interactive-blue));
  border-radius: 10px;
  transition: all 0.3s cubic-bezier(0.12, 0.08, 0.58, 1.91);
  opacity: 0;

  transform: scale(0.7);
}

.ac-selection-icons {
  z-index: 1;
  position: relative;
  width: 1.5rem;
  height: 1.5rem;
}

.ac-selection-field input:checked ~ .ac-selection-bg {
  opacity: 1;
  transform: scale(1);
}

.ac-selection-field span {
  margin: 10px 0px;
  margin-right: 10px;
  position: relative;
  z-index: 1;
  transition: all 0.2s cubic-bezier(0.12, 0.08, 0.58, 1.91);
}

.ac-selection-field .ac-selection-icons .ac-unchecked,
.ac-selection-field .ac-selection-icons .ac-checked {
  position: absolute;
  justify-content: center;
  align-items: center;
  transition: all 0.4s cubic-bezier(0.12, 0.08, 0.58, 1.91);
  display: flex;
}

.ac-selection-field .ac-selection-icons .ac-unchecked {
  opacity: 1;
}

.ac-selection-field .ac-selection-icons .ac-checked {
  transform: scale(0.3);
  color: white;
  opacity: 0;
}

.ac-selection-field input:checked ~ span {
  color: white;
}

.ac-selection-field input:checked ~ .ac-selection-icons .ac-unchecked {
  opacity: 0;
}

.ac-selection-field input:checked ~ .ac-selection-icons .ac-checked {
  opacity: 1;
  transform: scale(1);
}

.ac-selection-field input:checked:hover ~ .ac-selection-bg,
.ac-selection-field input:checked:hover ~ .ac-selection-icons .ac-checked {
  transform: scale(1.1);
}
.ac-selection-field input:hover ~ .ac-selection-icons .ac-unchecked {
  transform: scale(1.1);
  color: var(--color-primary);
}

.ac-slider-input-wrapper {
  --color-primary: #63aceb;
  --color-outline: #dee2e6;
}

.ac-slider-input-wrapper:hover {
  --color-outline: var(--color-primary);
}

.ac-slider-node {
  transition: border 0.2s ease-in-out;
  transition: transform 0.25s ease-in-out;
  justify-content: center;
  align-items: center;
  display: flex;
  width: 1.5rem;
  height: 1.5rem;
  color: white;
  border: 1.5px solid var(--color-outline);
  border-radius: 20px;
  position: relative;
  pointer-events: none;
  z-index: 2;
  transform: scale(0.8);
  background-color: white;
}

.ac-slider-input-wrapper:hover .ac-slider-node:not(.node-active):not(.node-true) {
  transform: scale(1);
}

.ac-slider-input-wrapper:hover .ac-slider-node:not(.node-true) {
  transform: scale(1.03);
}

.node-active.node-true {
  background-image: linear-gradient(158deg, #3f98e9, var(--interactive-blue));
  transform: scale(1.1);
  z-index: 5;
}

.node-active {
  background-color: var(--color-primary);
  border: 1.5px solid transparent;
  z-index: 4;
}

.ac-slider-outline {
  transition: border 0.2s ease-in-out;
  z-index: 1;
  width: 98%;
  height: 19px;
  pointer-events: none;
  background-image: Linear-gradient(to right, var(--color-primary) 50%, var(--color-outline) 50%);
  border-radius: 20px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  position: absolute;
  left: 5px;
  right: 5px;
}

.ac-slider-background {
  background-image: linear-gradient(to right, var(--color-primary) 50%, #ffffff 50%);
  z-index: 3;
  width: 97.5%;
  height: 16px;
  pointer-events: none;
  background-color: #fff;
  margin-left: auto;
  margin-right: auto;
  display: block;
  position: absolute;
  border-radius: 20px;
  left: 5px;
  right: 5px;
}

.ac-number-input::-webkit-inner-spin-button,
.ac-number-input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.ac-number-input::-ms-clear {
  display: none;
}

.ac-number-button {
  width: 40px;
  height: 40px;
  background-color: #1a4266;
  background-image: linear-gradient(170deg, #53adff, var(--interactive-blue));
  color: #fff;
  text-align: center;
  cursor: pointer;
  border-radius: 10px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 12px;
  font-weight: 600;
  display: flex;
  box-shadow: 0 3px 7px rgba(33, 134, 225, 0.36);
  transition: all 0.2s ease-in-out;
}

.ac-number-button[disabled='true'] {
  cursor: auto;
  background-image: none;
  background-color: transparent;
  color: var(--interactive-blue);
  opacity: 0.5;
  border: 2px solid var(--interactive-blue);
}

.ac-select-arrow {
  pointer-events: none;
  flex-direction: row;
  align-items: center;
  width: 16px;
  margin-right: 20px;
  transition: transform 0.3s cubic-bezier(0.24, 0.11, 0.47, 1.48);
  display: flex;
  position: absolute;
  top: 0%;
  bottom: 0%;
  left: auto;
  right: 0%;
  transform: rotate(180deg);
}

.ac-select-clear {
  pointer-events: none;
  flex-direction: row;
  align-items: center;
  width: 16px;
  display: inline-flex;
}

.inactive .ac-select-clear {
  display: none;
}
/* Styles for the embedded datepicker */
.embed .ac-datepicker-container {
  position: relative;
  top: auto;
  bottom: auto;
  left: 0;
  right: 0;
  width: 100%;
  max-width: 500px;
  height: auto;
  opacity: 1;
  pointer-events: auto;
  margin-top: 0;
  margin-left: auto;
  margin-right: auto;
  box-shadow: none;
  transform: none;
  transition: none;
  display: block;
  z-index: auto;
  background-color: transparent;
}

.embed .ac-datepicker-header-wrapper {
  justify-content: center;
  align-items: center;
  font-weight: 700;
  display: flex;
  position: relative;
}

.embed .ac-datepicker-days-wrapper {
  grid-template-columns: repeat(7, 1fr);
  grid-auto-rows: minmax(10px, auto);
  display: grid;
}

.embed .ac-datepicker-header-select-option:hover {
  transform: scale(1.07);
}

.embed .ac-datepicker-day {
  width: 100%;
  height: 100%;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px 7px;
  transition: transform 0.1s;
  display: flex;
}

.embed .ac-datepicker-day:hover {
  transform: scale(1.1);
}

.embed .ac-text-button {
  color: var(--color-primary);
  background-color: rgba(56, 152, 236, 0);
  justify-content: center;
  align-items: center;
  padding: 4px 10px;
  display: flex;
  cursor: pointer;
}

.embed .ac-text-button.disabled {
  opacity: 0.3;
  pointer-events: none;
  cursor: default;
}

/* Ensure proper embedding without modal behavior */
.embed .ac-datepicker-container {
  height: auto;
  display: block;
  opacity: 1;
  pointer-events: auto;
  margin-top: 0;
  position: relative;
  transform: none;
  transition: none;
}

.ac-datepicker-day.selected {
  background-color: var(--color-primary);
  color: white;
  border-radius: 50%;
}
